import React from 'react';
import { Link } from 'react-router-dom';


const Menu = () => {
    
    const state = { menuClassShow: false};

    const checkMenu = (e) => {
        if (state.menuClassShow === false){ 
            console.log("0");
            openMenu(e);
            state.menuClassShow = true;
        } else {
            closeMenu(e);
            state.menuClassShow = false;
        }
        
    }

    const openMenu = (e) => {
        e.preventDefault();
        document.querySelector(".menu").classList.remove("menu-outTransition");
        document.querySelector(".menu").classList.add("menu-transition");
        document.querySelector(".dog-profile").classList.add("left");
        document.querySelector(".menu-content").style.display ="block";
        document.querySelector(".servicelink").classList.add("left1");
        document.querySelector(".aboutlink").classList.add("left2");
        document.querySelector(".pricelink").classList.add("left3");
        document.querySelector(".testilink").classList.add("left4");
        document.querySelector(".contactlink").classList.add("left5");
        document.querySelector(".social").classList.add("left6");
        document.querySelector(".social").style.display ="block";

    }
    
    const closeMenu = (e) => {
        e.preventDefault();
        document.querySelector(".menu").classList.remove("menu-transition");
        document.querySelector(".menu").classList.add("menu-outTransition");
        document.querySelector(".dog-profile").classList.remove("left");
        document.querySelector(".menu-content").style.display ="none";
        document.querySelector(".menu-content").style.display ="none";
        document.querySelector(".social").style.display ="none"; 
    }
    
    return (
        <div className="menu m-0 h3 text-center"> 
            
            <div className="p-3 bg-dark" onClick={checkMenu}>
                <i class="fa fa-bars">MENU</i>
            </div>

            <div className="menu-content h6">

                <Link to="/">    
                    <div className="mt-5 mx-auto dog-profile"></div>
                </Link>

                <Link to="/Services">
                    <div className=" mt-5 btn-lg m-2 btn-light servicelink">
                        Services
                    </div>
                </Link>

                <Link to="/About">
                    <div className="btn-lg m-2 btn-light aboutlink">
                        About
                    </div>
                </Link>
                

                <Link to="/Price">
                    <div className="btn-lg m-2 btn-light pricelink">
                        Price
                    </div>
                </Link>

                <Link to="/Testimonials">
                    <div className="btn-lg m-2 btn-light testilink">
                        Testimonials
                    </div>
                </Link>

                <Link to="/Contact">
                    <div className="btn-lg m-2 btn-light contactlink">
                        Contact
                    </div>
                </Link>

            </div>

            <div className="justify-end mt-5 social">
                <i class="fa fa-twitter btn btn-outline-light m-2"></i>   
                <i class="fa fa-facebook btn btn-outline-light"></i>
            </div>
           
        </div>
    )
};

export default Menu;